<template>
  <b-card
    ref="cardAction"
    title="كافة العقود في الاسبوع"
  >
    <div class="container">
      <FullCalendar :options="calendarOptions" />
      <!--      <booking-details :obj="details" />-->
    </div>
  </b-card>
</template>

<script>

// import '@fullcalendar/core/vdom' // solves problem with Vite
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'
import { BCard } from 'bootstrap-vue'
import timeGridPlugin from '@fullcalendar/timegrid'
// import BookingDetails from './BookingDetailsPopup.vue'

export default {
  name: 'CalendarWeek',
  components: {
    FullCalendar, // make the <FullCalendar> tag available
    BCard,
    // BookingDetails,
  },
  data() {
    return {
      details: {},
      calendarOptions: {
        plugins: [dayGridPlugin, interactionPlugin, timeGridPlugin],
        // initialView: 'timeGridWeek',
        // dateClick: this.handleDateClick,
        eventClick: this.handleEventClick,
        // events: this.calendar,
        events: [
          {
            // color: 'yellow', // an option!
            // end: '2022-09-19 12:00:00',
            id: 137,
            start: '2022-09-19',
            title: 'Antibody Test AED 300',
          },
        ],
      },
    }
  },
  computed: {
    events() {
      return this.$store.state.contract.dates
    },
  },
  watch: {
    events() {
      this.calendarOptions.events = this.events
    },
  },
  created() {
    this.$store.dispatch('contract/dates').then(() => {
      // this.calendarOptions.events = res.data.data
    })
  },
  methods: {
    handleEventClick(arg) {
      this.showDetails(arg.event.id)
    },
    showDetails(id) {
      console.log(id)
      // this.$bvModal.show('BookingDetailsPopup')
      // this.$store.dispatch('bookingDetails/show', id).then(res => {
      //   this.details = res.data.data
      // })
    },
  },
}
</script>

